"use strict";

var Program = require('./Program');

var Shader = require('./Shader');

var Texture = require('./Texture');

var Script = require('./Script');

var WebGLCanvas = require('./WebGLCanvas');

var Class = require('uclass');

var vertexShaderScript = Script.createFromSource("x-shader/x-vertex", "\n  attribute vec3 aVertexPosition;\n  attribute vec2 aTextureCoord;\n  uniform mat4 uMVMatrix;\n  uniform mat4 uPMatrix;\n  varying highp vec2 vTextureCoord;\n  void main(void) {\n    gl_Position = uPMatrix * uMVMatrix * vec4(aVertexPosition, 1.0);\n    vTextureCoord = aTextureCoord;\n  }\n");
var fragmentShaderScript = Script.createFromSource("x-shader/x-fragment", "\n  precision highp float;\n  varying highp vec2 vTextureCoord;\n  uniform sampler2D YTexture;\n  uniform sampler2D UTexture;\n  uniform sampler2D VTexture;\n  const mat4 YUV2RGB = mat4\n  (\n   1.1643828125, 0, 1.59602734375, -.87078515625,\n   1.1643828125, -.39176171875, -.81296875, .52959375,\n   1.1643828125, 2.017234375, 0, -1.081390625,\n   0, 0, 0, 1\n  );\n\n  void main(void) {\n   gl_FragColor = vec4( texture2D(YTexture,  vTextureCoord).x, texture2D(UTexture, vTextureCoord).x, texture2D(VTexture, vTextureCoord).x, 1) * YUV2RGB;\n  }\n");
var YUVWebGLCanvas = new Class({
  Extends: WebGLCanvas,
  Binds: ['decode'],
  initialize: function initialize(canvas, size) {
    YUVWebGLCanvas.parent.initialize.call(this, canvas, size);
  },
  onInitShaders: function onInitShaders() {
    this.program = new Program(this.gl);
    this.program.attach(new Shader(this.gl, vertexShaderScript));
    this.program.attach(new Shader(this.gl, fragmentShaderScript));
    this.program.link();
    this.program.use();
    this.vertexPositionAttribute = this.program.getAttributeLocation("aVertexPosition");
    this.gl.enableVertexAttribArray(this.vertexPositionAttribute);
    this.textureCoordAttribute = this.program.getAttributeLocation("aTextureCoord");
    ;
    this.gl.enableVertexAttribArray(this.textureCoordAttribute);
  },
  onInitTextures: function onInitTextures() {
    console.log("creatingTextures: size: " + this.size);
    this.YTexture = new Texture(this.gl, this.size);
    this.UTexture = new Texture(this.gl, this.size.getHalfSize());
    this.VTexture = new Texture(this.gl, this.size.getHalfSize());
  },
  onInitSceneTextures: function onInitSceneTextures() {
    this.YTexture.bind(0, this.program, "YTexture");
    this.UTexture.bind(1, this.program, "UTexture");
    this.VTexture.bind(2, this.program, "VTexture");
  },
  fillYUVTextures: function fillYUVTextures(y, u, v) {
    this.YTexture.fill(y);
    this.UTexture.fill(u);
    this.VTexture.fill(v);
  },
  decode: function decode(buffer, width, height) {
    if (!buffer) return;
    var lumaSize = width * height;
    var chromaSize = lumaSize >> 2;
    this.YTexture.fill(buffer.subarray(0, lumaSize));
    this.UTexture.fill(buffer.subarray(lumaSize, lumaSize + chromaSize));
    this.VTexture.fill(buffer.subarray(lumaSize + chromaSize, lumaSize + 2 * chromaSize));
    this.drawScene();
  },
  toString: function toString() {
    return "YUVCanvas Size: " + this.size;
  }
});
module.exports = YUVWebGLCanvas;