"use strict";

var Class = require('../');

var guid = require('mout/random/guid');

var forIn = require('mout/object/forIn');

var EventEmitter = new Class({
  Binds: ['on', 'off', 'once', 'emit'],
  callbacks: {},
  initialize: function initialize() {
    var self = this;
    this.addEvent = this.on;
    this.removeListener = this.off;
    this.removeAllListeners = this.off;
    this.fireEvent = this.emit;
  },
  emit: function emit(event, payload) {
    if (!this.callbacks[event]) return;
    var args = Array.prototype.slice.call(arguments, 1);
    forIn(this.callbacks[event], function (callback) {
      callback.apply(null, args);
    });
  },
  on: function on(event, callback) {
    if (typeof callback != "function") return console.log("you try to register a non function in ", event);
    if (!this.callbacks[event]) this.callbacks[event] = {};
    this.callbacks[event][guid()] = callback;
  },
  once: function once(event, callback) {
    var self = this;

    var once = function once() {
      self.off(event, once);
      self.off(event, callback);
    };

    this.on(event, callback);
    this.on(event, once);
  },
  off: function off(event, callback) {
    if (!event) this.callbacks = {};else if (!callback) this.callbacks[event] = {};else forIn(this.callbacks[event] || {}, function (v, k) {
      if (v == callback) delete this.callbacks[event][k];
    }, this);
  }
});
module.exports = EventEmitter;